@import "https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap";
:root {
  --white: #fff;
  --light-blue: #f2feff;
  --blue: #92e1f1;
  --grey-blue: #306f8a;
  --grey: #767f8d;
  --light-grey: #a3b9d0;
  --standard-width: 800px;
  --spacing-xs: 2px;
  --spacing-sm: 4px;
  --spacing-md: 8px;
  --spacing-lg: 16px;
  --spacing-xl: 32px;
  --shadow: 2px 2px 3px #0000001f;
}

body {
  margin: 0;
  font-family: Nunito Sans, sans-serif;
}

.app {
  min-height: 100vh;
  padding: var(--spacing-md);
  box-sizing: border-box;
  background-image: linear-gradient(var(--light-blue), var(--blue));
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  width: 100%;
  max-width: var(--standard-width);
  color: var(--grey-blue);
  padding: 0 var(--spacing-md);
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  letter-spacing: -.05rem;
  font-size: 3rem;
  font-weight: bold;
}

.header .github {
  height: fit-content;
  font-size: 2.3rem;
  display: inline-block;
}

.searchbar-wrapper {
  width: 100%;
  max-width: var(--standard-width);
}

.searchbar {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  position: relative;
}

.searchbar .search-input-form {
  width: 100%;
}

.searchbar input[name="search"] {
  height: 40px;
  width: 100%;
  padding: 0 calc(var(--spacing-xl) * 1.5);
  box-sizing: border-box;
  box-shadow: var(--shadow);
  color: var(--grey-blue);
  border: none;
  border-radius: 30px;
  font-size: 1.3rem;
}

.searchbar input[name="search"]:focus {
  outline: none;
}

.searchbar button {
  color: var(--grey-blue);
  cursor: pointer;
  background-color: #0000;
  border: none;
}

.searchbar .search, .searchbar .geolocation {
  font-size: 1.4rem;
  position: absolute;
  top: 7px;
}

.searchbar .search {
  right: calc(var(--spacing-lg) * .6);
}

.searchbar .geolocation {
  left: calc(var(--spacing-lg) * .6);
}

.container {
  width: 100%;
  max-width: var(--standard-width);
}

.current-weather-wrapper {
  width: 100%;
}

.current-weather {
  width: 100%;
  background-color: var(--white);
  margin: var(--spacing-lg) 0;
  padding: var(--spacing-lg);
  box-sizing: border-box;
  box-shadow: var(--shadow);
  border-radius: 20px;
  flex-direction: column;
  animation-name: opacity;
  animation-duration: 1.5s;
  display: flex;
}

.current-weather-header {
  padding: var(--spacing-sm);
  margin: var(--spacing-sm);
  color: var(--grey);
  font-size: 1.5rem;
}

.current-weather-info {
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.current-weather-info .info-left {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-left .city-name {
  color: var(--grey-blue);
  font-size: 1.4rem;
}

.info-left .city-temp {
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  display: flex;
}

.city-temp .city-temp-value {
  color: var(--grey-blue);
  margin: 0;
}

.info-left .city-temp img {
  width: 90px;
  height: 90px;
}

.info-left .description {
  text-transform: capitalize;
  color: var(--grey-blue);
  font-size: 1.35rem;
}

.current-weather-info .info-right {
  color: var(--grey-blue);
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.info-right .feel {
  color: var(--grey-blue);
  font-size: 1.2rem;
}

.info-right i {
  color: var(--light-grey);
  align-self: center;
}

.info-right .value {
  font-weight: bold;
}

.info-right .hwp {
  width: 220px;
  margin: var(--spacing-sm) 0;
  grid-template-columns: 35px 1fr 1fr;
  display: grid;
}

.info-right .temps {
  margin-bottom: var(--spacing-lg);
  display: flex;
}

.info-right .temp {
  width: 60px;
  grid-template-columns: 20px 1fr;
  display: grid;
}

.forecast {
  box-shadow: var(--shadow);
  width: 100%;
  max-width: var(--standard-width);
  padding: var(--spacing-lg);
  box-sizing: border-box;
  color: var(--grey-blue);
  background-color: #fffc;
  border-radius: 20px;
  animation-name: opacity;
  animation-duration: 1.5s;
}

.forecast-header {
  padding: var(--spacing-sm);
  margin: var(--spacing-sm);
  color: var(--grey);
  font-size: 1.5rem;
}

.forecast-days {
  grid-template-columns: repeat(5, 1fr);
  display: grid;
}

.forecast-days .day {
  border-right: 1px solid var(--light-grey);
  padding: var(--spacing-sm);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.forecast-days .day:nth-child(5) {
  border-right: none;
}

.day img {
  height: 90px;
  width: 90px;
}

.day .desc {
  text-transform: capitalize;
}

.day span {
  margin-bottom: var(--spacing-lg);
  font-weight: bold;
}

@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 710px) {
  .forecast-days {
    grid-template-columns: repeat(3, 1fr);
  }

  .forecast-days .day:nth-child(3) {
    border-right: none;
  }

  .forecast-days .day:nth-child(5) {
    border-right: 1px solid var(--light-grey);
  }
}

@media only screen and (max-width: 570px) {
  .info-left .city-temp {
    font-size: 2rem;
  }

  .info-left .city-temp img {
    width: 70px;
    height: 70px;
  }
}

@media only screen and (max-width: 500px) {
  .current-weather-info {
    grid-template-columns: 1fr;
  }

  .current-weather-info .info-right {
    align-items: center;
  }

  .info-right .temps {
    width: 150px;
    justify-content: space-between;
  }

  .info-right .hwp {
    width: 200px;
  }

  .forecast-days {
    grid-template-columns: repeat(2, 1fr);
  }

  .forecast-days .day:nth-child(3), .forecast-days .day:nth-child(5) {
    border-right: 1px solid var(--light-grey);
  }

  .forecast-days .day:nth-child(2n) {
    border: none;
  }
}

@media only screen and (max-width: 370px) {
  .forecast-days {
    grid-template-columns: 1fr;
  }

  .forecast-days .day {
    border-right: none !important;
    border-bottom: 1px solid var(--light-grey) !important;
  }

  .forecast-days .day:nth-child(5) {
    border: none !important;
  }
}

